@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu{position:fixed;width:15%;min-width:150px;height:100%;background:#FAFBFD;z-index:1}@media (max-width: 800px){.menu{display:none}}.menu__title{text-align:center;font-family:'Open Sans', sans-serif;font-style:normal;font-weight:600;font-size:13px;line-height:19px;margin:40px 0 0 0}.menu__title--dev{color:#F7542E}.menu__list{width:85px;margin:0;padding:80px 0;text-align:left;position:relative;left:70px}.menu__link{font-family:'Noto Sans JP', sans-serif;font-style:normal;font-weight:500;font-size:14px;line-height:20px;color:#9E9E9E;list-style:none;padding:20px 0}.menu__link--active{font-family:'Noto Sans JP', sans-serif;font-style:normal;font-weight:500;font-size:14px;line-height:20px;list-style:none;font-weight:bold;color:#090F31;padding:25px 0}

.inputsDisplayer{padding:40px 0px 40px 50px}@media (max-width: 800px){.inputsDisplayer{padding:40px 10px}}.inputsDisplayer__title{font-family:'Open Sans', sans-serif;font-style:normal;font-weight:500;font-size:24px;line-height:36px;color:#4F4F4F;margin:0 0 40px 0}.inputsDisplayer__row{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}

.inputWrapper{margin:25px 80px 25px 0}.inputWrapper__description{font-family:'Ubuntu Mono', monospace;font-style:normal;font-weight:normal;font-size:12px;line-height:12px;color:#333333;margin:0 0 25px 0}.inputWrapper__description--pseudoclass{font-family:'Ubuntu Mono', monospace;font-style:normal;font-weight:normal;font-size:12px;line-height:12px;color:#828282;margin:0 0 25px 0}.inputWrapper--full{width:95%}

.input{width:200px;height:56px;border:1px solid #828282;border-radius:8px;box-sizing:content-box;padding:0;margin:0;font-family:'Noto Sans JP', sans-serif}.input input{position:relative;top:-18px;margin:0;padding:0 15px;width:80%;height:100%;border-radius:8px;border:none;background:rgba(0,0,0,0);font-weight:500;font-size:14px;line-height:20px;color:#333;font-family:'Noto Sans JP', sans-serif}.input label{display:block;position:relative;padding:0;margin:0;top:-18px;color:#828282;font-style:normal;font-weight:normal;font-size:12px;line-height:17px}.input .helperText{position:relative;top:-15px;color:#828282;font-style:normal;font-weight:normal;font-size:10px;line-height:14px}.input--hover{pointer-events:none;border:1px solid #333}.input--hover label{color:#333}.input--focus{pointer-events:none;border:1px solid #2962FF}.input--focus label{color:#2962FF}.input:hover{border:1px solid #333}.input:hover label,.input:hover .helperText,.input:hover .startIcon,.input:hover .endIcon{color:#333}.input:focus-within{border:1px solid blue}.input:focus-within label,.input:focus-within .helperText,.input:focus-within .startIcon,.input:focus-within .endIcon{color:#2962FF}.input--error{border:1px solid #D32F2F}.input--error label,.input--error .helperText{color:#D32F2F}.input--error:focus-within{border:1px solid #D32F2F}.input--error:focus-within label,.input--error:focus-within .helperText{color:#D32F2F}.input--error.input--hover{border:1px solid #333}.input--error.input--hover label,.input--error.input--hover .helperText{color:black}.input--error .input--hover+label{color:black}.input--disabled{pointer-events:none;background:#F2F2F2;border:1px solid #E0E0E0}.input--disabled:hover{border:1px solid #E0E0E0}.input--start-icon .startIcon,.input--start-icon .endIcon,.input--end-icon .startIcon,.input--end-icon .endIcon{display:inline-block;position:relative;top:-12px;color:#828282}.input--start-icon input,.input--end-icon input{display:inline-block;width:65%}.input--sm{height:40px}.input--md{height:56px}.input--lg{height:70px}.input--fullWidth{width:100%}.input textarea{position:relative;top:-18px;margin:0;padding:0 15px;width:80%;height:100%;border-radius:8px;border:none;background:rgba(0,0,0,0);font-weight:500;font-size:14px;line-height:20px;font-family:'Noto Sans JP', sans-serif;color:#333;resize:none}.input.input--multiline{padding-top:1px;height:90%}::-moz-focus-inner{border:none}:focus{outline:none}.material-icons{position:relative;top:3px;font-size:1.3em;margin:0 8px}

.footer{font-family:'Montserrat', sans-serif;font-style:normal;font-weight:500;font-size:14px;line-height:17px;text-align:center;color:#A9A9A9}.footer__link{font-weight:bold;color:#A9A9A9}

*{margin:0}.inputsDisplayer{margin-left:15%;width:80%}@media (max-width: 800px){.inputsDisplayer{margin-left:0;width:100%}}

